import { Router } from "@reach/router";
import { Header, MainPage } from "../index";
import styles from "./App.module.scss";
import { routeUrl } from "../../utils/constants";

const basepath = routeUrl === "" ? "/" : routeUrl;

const App = () => {
  return (
    <div className={styles.rootPage}>
      <Header />
      <Router>
        <MainPage path={basepath} />
      </Router>
    </div>
  );
};

export { App };
